import React from 'react'
import { Bio } from './style'
import ReactPlayer from 'react-player'

const Biography = () => {
    return (
        <div>
            <Bio className="section">
                <div className="container">
                    <h2 className="title">Biografia</h2>
                    <p className="text">
                        DJ Sandinho  um nome conhecido no mundo da msica,
                        especialmente no universo do funk carioca. Apelidado de
                        O Alquimista das Aceleradas, ele  conhecido por suas
                        misturas incrveis e seu ritmo louco.
                        <br />
                        <br />
                        Seu amor pelo drum n bass  evidente em suas
                        apresentaes, onde ele mistura batidas rpidas e
                        intensas com a tcnica de mixagem que domina. Com anos
                        de experincia na indstria da msica, DJ Sandinho  um
                        mestre em seu ofcio. Sua tcnica de mixagem 
                        incomparvel, e ele  capaz de transformar qualquer
                        msica em uma experincia mgica no verdadeiro baile.
                        Sua paixo pela msica e sua habilidade em criar magias
                        sonoras so evidentes em todas as suas apresentaes. Se
                        voc ainda no teve a oportunidade de experimentar a
                        msica de DJ Sandinho, no perca mais tempo. Ele  um
                        artista nico, capaz de transformar qualquer evento em
                        um verdadeiro baile. Com seu estilo inovador e sua
                        habilidade tcnica, DJ Sandinho  um dos artistas mais
                        talentosos e promissores da cena musical atual. No
                        deixe de conferir seu prximo show!
                    </p>
                </div>
            <ReactPlayer className="player" url='https://www.youtube.com/watch?v=QyGY5uJRVV8' />
            </Bio>
        </div>
    )
}

export default Biography
